import { useMemo } from "react";
import { useTranslation } from "react-i18next";

import InnerRoutes from "./Routes";
import classes from "./Contractor.module.scss";
import { Container, PageMenu, PageTitle } from "components/shared";
import { useLocation } from "react-router-dom";

const Contractor = () => {
	const { t } = useTranslation();
	const { pathname } = useLocation();

	const menus = useMemo(
		() => [
			{ href: "/contractor/complexes", label: t("Мажмуалар") },
			{ href: "/contractor/buildings", label: t("Бинолар") },
			{ href: "/contractor/apartments", label: t("Хонадонлар") }
		],
		[t, pathname]
	);

	return (
		<div className={classes.main}>
			<Container>
				<PageTitle title={t("Мени келажакдаги уйим")} />

				<PageMenu menus={menus.map(({ href, ...item }) => ({ ...item, href, isActive: pathname.startsWith(href) }))} />

				<InnerRoutes />
			</Container>
		</div>
	);
};

export default Contractor;
