import { lazy } from "react";
import { NotFound } from "components";
import { useRoutes } from "react-router-dom";

// Private Routes
const Complexes = lazy(() => import("./pages/Complexes"));
const Buildings = lazy(() => import("./pages/Buildings"));
const Apartments = lazy(() => import("./pages/Apartments"));
const CreateApartment = lazy(() => import("./pages/Buildings/components/CreateApartment"));

export default function InnerRoutes() {
	return useRoutes([
		{
			path: "/complexes",
			element: <Complexes />
		},
		{
			path: "/buildings",
			element: <Buildings />
		},
		{
			path: "/apartments",
			element: <Apartments />
		},
		{
			path: "/buildings/create-apartment",
			element: <CreateApartment />
		},
		{
			path: "*",
			element: <NotFound />
		}
	]);
}
